import React from 'react';

import styles from './NotFound.module.css'





const NotFound: React.FC = () => {
   return (


      <main className={styles.root}>
         <div className={styles.title_block}>
            <h1 className={styles.title}>Ничего не найдено</h1>
            <img className={styles.smile} src="./img/cart-empty/smile.png" alt="" />
         </div>
         <p className={styles.subtitle}>К сожалению данная страница отсутствует в нашем интернет-магазине</p>
      </main>


   );
}

export default NotFound;